:root {
  --black-color: #212121;
  --gray-color: #757575;
  --logo-color: #afb1b8;
  --active-link-color: #2196f3;
  --white-color: #fff;
  --blue-color: #0039f5;
  --header-color: #linear-gradient(90deg, #020024 0%, #090979 37%, #00d4ff 100%);
  --container-color: #006deb;
  --body-color: #111;
}

.hiddenh2, .visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: 0;
  visibility: hidden;
}

*, :after, :before {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

ul, li {
  list-style: none;
}

button {
  border: none;
}

img {
  display: block;
}

input, textarea {
  outline: none;
}

body {
  background-color: #fae9f8;
}

.body-container {
  max-width: 1200px;
  height: 100vh;
  margin: 0 auto;
}

.make-absolute {
  position: absolute;
}

.header-section {
  width: 100vw;
  background-color: #74ebd5;
  background-image: linear-gradient(90deg, #74ebd5 0%, #9face6 100%);
}

.header-container {
  max-width: 900px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 15px 0;
  display: flex;
}

.logo-section {
  color: var(--black-color);
  flex-direction: row;
  align-items: center;
  font-size: 40px;
  display: flex;
}

.site-logo {
  width: 70px;
  height: 70px;
  border-radius: 75px;
  margin-right: 10px;
}

.nav-list {
  flex-direction: row;
  display: flex;
}

.nav-list__button {
  color: var(--black-color);
  background-color: rgba(0, 0, 0, 0);
  font-size: 20px;
}

.nav-list__item:not(:last-child) {
  margin-right: 30px;
}

.header__login-btns-container {
  width: 70px;
  display: flex;
  position: relative;
}

.header__login-btn {
  color: var(--black-color);
  background-color: rgba(0, 0, 0, 0);
  font-size: 20px;
}

.modal-login {
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(65, 65, 65, .443);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
}

.modal-close {
  width: 30px;
  height: 30px;
  border: 1px solid #000;
  border-radius: 15px;
  position: absolute;
  top: 2%;
  right: 2%;
}

.login-form {
  max-width: 500px;
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  display: flex;
  position: relative;
}

.login-form-input {
  width: 150px;
  box-sizing: border-box;
  border: 2px solid #dfdede;
  outline: none;
  padding: 2px 0 0 7px;
}

.login-form-input:hover, .login-form-input:focus {
  border-color: var(--active-link-color);
}

.login-form__login {
  margin-bottom: 5px;
}

.login-button {
  color: #1b1b1b;
  border: 4px var(--blue-color) solid;
  width: 100px;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, .494);
  margin-top: 10px;
  padding: 5px;
  transition: all .5s;
}

.login-button:hover {
  background-color: var(--active-link-color);
  color: azure;
  border: 4px solid #000;
}

.order-section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  display: flex;
}

.order-form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  display: flex;
}

.order-form__input-current {
  width: 200px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid gray;
  margin: 10px 0;
  padding: 2px 0 0 5px;
  font-size: 15px;
}

.order-form__submit {
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #66d366;
  padding: 5px 15px;
  font-size: 15px;
}

.tour-btn {
  width: 150px;
  color: #000;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #00f;
  border-radius: 14px;
  padding: 10px;
  font-size: 30px;
}

.available-country-list {
  max-width: 900px;
  flex-flow: wrap;
  justify-content: space-between;
  display: flex;
}

.country-list__item {
  width: 200px;
  height: 200px;
  border: 1px solid #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: large;
  display: flex;
}

.country-list__btn {
  border: 2px solid #000;
  margin-top: 30px;
  padding: 5px 20px;
  font-size: 25px;
}

.order-slider {
  width: 800px;
  margin-bottom: 20px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.order-slider:hover, .order-slider:focus {
  transform: translateX(0%);
}

.slider__image-title {
  padding-left: 30px;
  font-size: 40px;
}

.slider__btn-container {
  padding-top: 20px;
  padding-right: 20px;
}

.slider__btn {
  width: 20px;
  height: 20px;
  background-color: #fff;
}

.slider__btn:not(:last-child) {
  margin-right: 10px;
}

.slider__btn-active, .slider__btn:hover, .slider__btn:focus {
  background-color: rgba(255, 0, 128, .753);
}

.slider__overlay-description {
  width: 100%;
  height: 400px;
  background-color: rgba(0, 255, 255, .663);
  font-size: 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
  overflow: auto;
  transform: translateX(101%);
}

.slider__image {
  width: 800px;
  height: 450px;
}

.slider__title-btns-container {
  width: 100%;
  height: 50px;
  background-color: rgba(0, 255, 255, .663);
  justify-content: space-between;
  display: flex;
  position: absolute;
  bottom: 0;
}

